@import "ScssHelpers/helpers-all.scss";

.featured_recipies {
	@include bp($point_4, min) {
		background-color: #f5f5f5;
	}
}

.featured_recipies__wrap {
	position: relative;
}

.featured_recipies__button {
	position: absolute;
	z-index: 3;
	top: 0;

	&.featured_recipies__button--prev_mod {
		left: 0;

		@include bp($point_2, min) {
			margin-left: -2.6rem;
		}
		@include bp($point_3) {
			margin-left: -2rem;
		}
	}
	&.featured_recipies__button--next_mod {
		right: 0;

		@include bp($point_2, min) {
			margin-right: -2.6rem;
		}
		@include bp($point_3) {
			margin-right: -2rem;
		}
	}

	@include bp($point_4, min) {
		margin-top: 9.8%;
	}
	@include bp($point_4 - 1) {
		margin-top: 19.4%;
	}
}

.featured_recipies__slider {
	&.swiper {
		margin-right: -2.4rem;
	}
}

.featured_recipies__item {
	padding-right: 2.4rem;
}